<template>
  <a-layout style="height: 100vh; width: 100%">

    <!-- 顶部标题 -->
    <a-layout-header style="width: 100%">
      <a-space style="margin-left: 2%;">
        <template #split><a-divider direction="vertical" /></template>

        <h1 style="font-size: 23px;">已审批的课程</h1>
        <a-button type="primary" :loading="get_all_loading" @click="get_courses">
          {{ get_all_loading ? "加载中" : "重新加载" }}
        </a-button>

      </a-space>
    </a-layout-header>

    <!-- 课程列表 -->
    <a-layout-content style="width: 100%">
      <a-table :columns="columns" :data="course_list" style="width: 100%;"
        :scroll="{ x: '100%', y: '100%' }" :expandable="expandable">
        <template #optional="{ record }">
          <!-- NOTR: record 是表里的每一行数据 -->

          <a-space v-show="record.status=='approved'">
            <a-tag color="green" bordered>
              已通过
            </a-tag>
            <a-button type="outline" size="mini"
              @click="goto_course_info_page(record.application_id)">
              详情
            </a-button>
          </a-space>

          <a-space v-show="record.status=='rejected'">
            <a-tag color="orangered" bordered>
              已否决
            </a-tag>
          </a-space>

        </template>
      </a-table>
    </a-layout-content>

  </a-layout>
</template>
<script>
import admin_api from "@/api/admin/admin-api"

export default {
  data() {
    return {
      /* INTRO: 管理员的id */
      user_id: "",

      /* 表头属性 */
      columns: [
        { title: '操作',        slotName: 'optional', width: 250, fixed: 'left' },
        { title: '申请序号',    dataIndex: 'application_id', width: 100, fixed: 'left' },
        { title: '课程名',      dataIndex: 'course_name' },
        { title: '授课教师',    dataIndex: 'teacher_info.user_name' },
        { title: '学分',        dataIndex: 'credit' },
        { title: '最大可选人数', dataIndex: 'max_student_number' },
      ],

      /* INTRO: 用于列表展示的课程信息 */
      course_list: [],
      /* 重新加载按钮的加载状态 */
      get_all_loading: false
    }
  },

  mounted() {
    this.user_id = this.$route.query.user_id
    this.get_courses();
  },

  methods: {
    /* INTRO: 设置重新加载按钮的加载状态 */
    set_get_all_btn_loading(is_loading) {
      this.get_all_loading = is_loading
    },

    /* INTRO: 获取课程信息 */
    get_courses() {
      this.set_get_all_btn_loading(true)

      /* INTRO: 发送请求 */
      admin_api.get_courses_not_pending_approval_list()
      .then(resp => {
        this.set_get_all_btn_loading(false)
        if (resp.status_code != 200) {
          this.$message.error("加载失败，" + resp.status_text)
          return
        }

        this.course_list = resp.data
        console.log(resp.data)
      })
      .catch(err => {
        this.set_get_all_btn_loading(false)
        this.$message.error("加载失败，出现异常")
        console.error(err)
      })
    },

    goto_course_info_page(course_id) {
      let query_data = { user_id: this.user_id, course_id }
      this.$router.push({ path: "/admin/course/info", query: query_data })
    }
  }
}
</script>
  