import http_req from "@/utils/http-req"

export default {
  /* INTRO: 注册学生账号 */
  register_student(register_student_DTO/* : {
    user_id         : String,
    passwd          : String,
    name            : String,
    gender          : String,
    department      : String,
    major           : String,
    enrollment_date : String,
    created_by      : String
  } */) {
    return http_req({
      url: "/admin/register_student",
      method: "post",
      data: register_student_DTO
    })
  },

  /* INTRO: 获取院系和专业 */
  get_department_and_major() {
    return http_req({
      url: "/info/department_and_major",
      method: "get"
    })
  },

  /* INTRO: 注册教师账号 */
  register_teacher(register_teacher_DTO/* : {
    user_id    : String,
    passwd     : String,
    name       : String,
    gender     : String,
    created_by : String
  } */) {
    return http_req({
      url: "/admin/register_teacher",
      method: "post",
      data: register_teacher_DTO
    })
  },

  /* INTRO: 注册管理员账号 */
  register_admin(register_admin_DTO/* : {
    user_id    : String,
    passwd     : String,
    name       : String,
    gender     : String,
    created_by : String
  } */) {
    return http_req({
      url: "/admin/register_admin",
      method: "post",
      data: register_admin_DTO
    })
  },

  /* INTRO: 获取待审批的课程列表 */
  get_courses_pending_approval_list() {
    return http_req({
      url: "/admin/course/pending_list",
      method: "get"
    })
  },

  /* INTRO: 审批课程申请 */
  approve_course_application(approve_course_application_DTO/* : {
    application_id : Long,
    approve_result : enum('approved', 'rejected')
    admin_id       : String,
  } */) {
    return http_req({
      url: "/admin/course/approve_application",
      method: "post",
      data: approve_course_application_DTO
    })
  },
  
  /* INTRO: 获取已审批的课程列表 */
  get_courses_not_pending_approval_list() {
    return http_req({
      url: "/admin/course/not_pending_list",
      method: "get"
    })
  },

  /* INTRO: 通过课程号获取课程详情 */
  get_course_info_by_id(get_course_info_DTO/* : {
    course_id : String
  } */) {
    return http_req({
      url: "/admin/course/info",
      method: "get",
      params: get_course_info_DTO
    })
  }
}
